
import common from '@/libs/common.mixin'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default{
mixins: [common],
components:{
Group,
Cell,
Flexbox,
FlexboxItem
},
data () {
return {
data: []
}
},
mounted () {
this.init_data()
},
methods: {
init_data () {
this.$rest('edu_growups?with=student,one_class').get()
.success(data=>{
console.log('data')
console.log(data)
this.data = data.list
}).error(response=>{
this.toast(response.body.message,'text')
})
}
}
}



